<template>
  <section class="card-group row w-tab">
    <!-- 左邊 -->
    <div class="card col-3 pt-3">
      <div class="card-body">
        <template v-for="(item, index) in menuList" :key="index">
          <div class="position-relative text-center mb-4">
            <router-link :to="`/user/${item}`" class="text-decoration-none text-uppercase">
              {{ item }}
              <CircleTwo :class="{ 'd-none': item !== this.$route.path.split('/')[2] }" />
            </router-link>
          </div>
        </template>
      </div>
    </div>
    <!-- 右邊 -->
    <div class="card col-9">
      <div class="card-body">
        <router-view />
      </div>
    </div>
  </section>
</template>
<style lang="scss" scoped>
.circle-2 {
  width: 6rem;
}
</style>

<script>
import CircleTwo from '@/components/models/circle/CircleTwo.vue';

export default {
  data() {
    return {
      select: 'PROFILE',
      menuList: ['profile', 'favorite', 'order', 'coupon', 'password', 'logout'],
      user: {},
    };
  },
  components: {
    CircleTwo,
  },
  methods: {
    getUser() {
      const api = `${process.env.VUE_APP_API2}/api/user/profile`;
      this.$http
        .get(api)
        .then((res) => {
          this.user = res.data.data;
          this.isLogin = !this.isLogin;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    modifyUser(data) {
      const api = `${process.env.VUE_APP_API2}/api/user/profile`;
      this.$http
        .patch(api, data)
        .then((res) => {
          console.log(res);
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>
